import { Injectable } from '@angular/core';
import { EscBuilder } from './encode/EscBuilder';
@Injectable({
  providedIn: 'root'
})
export class VhEscposService {

  constructor() { }
  public builder: EscBuilder;

  /**
   * Initialize a new print queue
   */
  init(): VhEscposService {
    this.builder = new EscBuilder();
    this.builder.init();
    return this;
  }

  /**
   *
   * @param cutType full|partial
   */
  public cut(cutType: string = 'full'): VhEscposService {
    this.builder.cut(cutType);
    return this;
  }

  /**
   *
   * @param lineCount How many lines to feed
   */
  public feed(lineCount: number = 1): VhEscposService {
    this.builder.feed(lineCount);
    return this;
  }

  setInverse(value: boolean = true): VhEscposService {
    this.builder.setInverse(value);
    return this;
  }

  setBold(value: boolean = true): VhEscposService {
    this.builder.setBold(value);
    return this;
  }

  setUnderline(value: boolean = true): VhEscposService {
    this.builder.setUnderline(value);
    return this;
  }

  /**
   *
   * @param value left|center\right
   */
  setJustification(value: string = 'left'): VhEscposService {
    this.builder.setJustification(value);
    return this;
  }

  /**
   *
   * @param value normal|large
   */
  setSize(value: string = 'normal'): VhEscposService {
    this.builder.setSize(value);
    return this;
  }

  /**
   *
   * @param text
   */
  writeLine(text: string = ''): VhEscposService {
    this.builder.writeLine(text);
    return this;
  }

  /**
   *
   *
   */
  drawLine(): VhEscposService {
    this.builder.drawLine();
    return this;
  }
  /**
   *
   * @param data
   */
  writeTable(data: any): VhEscposService {
    this.builder.writeTable(data);
    return this;
  }
  /**
   *
   * @param size
   */
  setPageSize(size: number = 48): VhEscposService {
    this.builder.setPageSize(size);
    return this;
  }

  writeCustomTable(data: any, options: any = {}): VhEscposService {
    this.builder.writeCustomTable(data, options);
    return this;
  }

  /**
   * write the current builder value to the driver and clear out the builder
   */
  flush() {
    return this.builder.flush();
  }
}
